import { Modal } from "../Modal/Modal";
import "./Search.css"

export const Search = ({ setFilterValue, filterValue, onAdd, editItem, setEditItem }) => {

    return (
        <div className="search__container">
            <img
                alt="plus"
                src="data:image/png;base64,"
                className="modal__add__img"
                onClick={() => setEditItem({ addModal: true })}
            />
            <label htmlFor="">
                Шукати:
                <input
                    className="input__search"
                    type='search'
                    value={filterValue}
                    onChange={(e) => setFilterValue(e.target.value)}
                />
            </label>
            <Modal setActive={setEditItem} active={editItem.addModal}>
                <h2>You can add goods!</h2>
                <form className="form" method="get" onSubmit={onAdd}>
                    <label>
                        Name:
                        <textarea
                            className="form__item"
                            value={editItem.name}
                            onChange={(e) => setEditItem({...editItem, name: e.target.value})}
                        />
                    </label>
                    <label>
                        Price:
                        <input
                            className="form__item"
                            value={editItem.price}
                            onChange={(e) => setEditItem({...editItem, price: e.target.value})}
                        />
                    </label>
                    <button type="submit" className="form__button">Add</button>
                </form>
            </Modal>
        </div>
    );
};